import React from 'react'
// import Navigator from './Navigator'
import { NavLink } from 'react-router-dom'
import './Navigators.css'

const Navigators = props => {
    let navigators = null;
    navigators = props.navigators.map((item, itemIndex) => {
        return (
            <NavLink key={itemIndex} className='NavigatorLink' activeClassName='active' to={item.link}>{item.label}</NavLink>
        )
    });

    return (
        <div className='Navigators'>{navigators}</div>
    )
}

export default Navigators;