import React from 'react'

const Container = (props) => {
    const defaultStyle = {
        overflow: 'hidden',
        margin: 'auto',
    }
    
    let containerStyle = defaultStyle;
    if(props.style){
        containerStyle = props.style;
    }

    return(
        <div style={containerStyle} className='container'>{props.children}</div>
    )
}

export default Container;