import React from 'react'
import ReactDOM from 'react-dom';
import { Route } from 'react-router-dom';
import './Projects.css'

const SampleComponent = () => {
    return (
        <div>
            <h1>Sample Component! </h1>
        </div>
    );
}

const Project = (props) => {
    const renderModal = function (url) {
        document.getElementById('modal').style.display = 'block';
        document.body.style.overflow = 'hidden';
        ReactDOM.render(
        <div style={{position: 'relative', margin: '12% auto'}}>
            <img className='LargeImage' src={url}/>
        </div>,
        document.getElementById('modal'))
    }

    const contents = props.screenShots.map(
        (url, urlIndex)=>{
            return (
                <div className="Portrait" key={urlIndex} onClick={() => renderModal(url)}><img className="GalleryImage" src={url} alt={props.name}/></div>
            )
        }
    )

    return (
        <div className="Project">
            <Route path={'/projects/'+props.name}>
            <h1 className='ProjectName'>{props.name}</h1>
            <div className='ProjectContent'>
                <p>{props.description}</p>
                {contents}
            </div>
            </Route>
        </div>
    )
}

export default Project;