import React from 'react'

const Years = (props) =>{
    const style = {
        fontSize: '10px',
        color: 'grey',
        margin: '0',
        display: 'block',
    }
    return(
        <span style={style}>{props.start}-{props.end}</span>
    )
}

export default Years