import React from 'react'

const BulletList = (props) =>{
    const list = (props.items.map((item, itemIndex) => {
        return (
            <li key={itemIndex}>{item}</li>
        )
    }));

    return(
        <div>
            <h3>{props.title}</h3>
            <ul>
                {list}
            </ul>
        </div>
    )
}

export default BulletList