import React from 'react'
import Container from './Container.js';

const Footer = (props) =>{
    const style={
        position: 'relative',
        height: '100%',
        textAlign: 'center',
        fontSize: '10px',
        bottom: '0',
    }

    let currentYear = new Date().getFullYear();
    return(
        <div style={style}>
            <Container>
                <p>&copy;{currentYear} Lin H. Chen</p>
            </Container>
        </div>
    )
}

export default Footer;