import React, {Component} from 'react'
import Container from '../Container'
import Project from './Project'
import { NavLink, Redirect } from 'react-router-dom'

class Projects extends Component{
    containerStyle = {
        position: 'relative',
        textAlign: 'center',
        backgroundColor: 'white',
        borderRadius: '10px',
        boxShadow: '3px 3px 5px grey',
        overflow: 'hidden',
        display: 'inline-block',
        width: '85%',
        height: '75vh',
        minHeight: '800px',
        zIndex: '5'
    }

    toggleProjectContentHandler = (event) =>{
        // console.log('[toggleProject] clicked project name, toggle project content ');
        // console.log(event.target);
        let content = event.target.nextSibling;
        content.style.display = content.style.display ? '' : 'block';
    }

    closeModal = () => {
        document.getElementById('modal').style.display = 'none';
        document.body.style.overflow = 'auto';
    }

    render(){
        const projects = this.props.projects.map((project, projectIndex)=>{
            return (
                <Project 
                    key={projectIndex}
                    name={project.name} 
                    description={project.description} 
                    screenShots={project.screenShots} 
                    // clicked={this.toggleProjectContentHandler}
                />
            )
        })

        const sidebar = this.props.projects.map((project, index) => {
            return (
                <NavLink key={index} className="NavLink" activeClassName="active" to={'/projects/'+project.name}>{index+1}</NavLink>
            )
        })
        return (
            <div className='reg_project'>
                <div id="modal" onClick={this.closeModal}></div>
                <div id='sidebar'>
                    {sidebar}
                </div>
                <Redirect from="/projects" to={'/projects/'+this.props.projects[0].name}/>
                <Container style={this.containerStyle}>
                    <div style={{overflowY: 'scroll', height: '70vh'}}>
                        {projects}
                    </div>
                </Container>
            </div>
        )
    }
}

export default Projects;