import React from 'react';
import Years from './Years';

const Educations = (props) => {
    const style = {
        margin: '10px auto',
        listStyle: 'none'
    }

    const educations = props.educationsInfo.map((school, schoolIndex)=>{
        return (
            <p style={style} key={schoolIndex}>
                <strong>{school.name}</strong>
                <Years start={school.start} end={school.end} />
                {school.degree}
            </p>
        )
    });
    return (
        <div>
            <h2>Educations:</h2>
            {educations}
        </div>
    )
}

export default Educations;