import React from 'react';
import Container from '../Container';
import BulletList from './BulletList';
import Educations from './Educations';
import Contacts from './Contact';
import './resume_button.css'

const Profile = (props) =>{
    const style = {
        margin: 'auto',
        width: '85%',
    }

    const containerStyle = {
        // width: '90%',
        // overflow: 'hidden',
        position: "relative",
        margin: 'auto',
        textAlign: 'center',
        backgroundColor: 'white',
        borderRadius: '10px',
        boxShadow: '3px 3px 5px grey',
        display: 'inline-block',
        height: '75vh',
        minHeight: '800px'
    }

    const style_half = {
        display: 'inline-block',
        width: '40%',
        verticalAlign: 'top',
        textAlign: 'left',
        margin: 'auto 10px',
        padding: '30px',
    }

    const style_children = {
        fontSize: '23px',
        width: '400px',
        fontWeight: '300',
    }

    return (
        <div className="reg_profile" style={style}>
            <Container style={containerStyle}>
                <div style = {style_half}>
                    <h1>{props.name}</h1>
                    <p style={style_children}>{props.children}</p>
                </div>
                <div style = {style_half}>
                    <a className="ResumeButton" href="./resume.pdf">Resume</a>
                </div>
                <div style = {style_half}>
                    <BulletList items = {props.languages} title="Skills: " />
                    <BulletList items = {props.skills} title="Others:" />
                </div>
                <div style = {style_half}>
                    <Educations educationsInfo = {props.educationsInfo} />
                    <br/>
                    <br/>
                    <Contacts contactInfo = {props.contactInfo} />
                </div>
            </Container>
        </div>
    )
}

export default Profile;