import React from 'react'
import RegButton from '../RegButton'

const Contacts = (props) =>{
    const style = {
        width: '300px',
        borderTop: '1px solid black',
        borderBottom: '1px solid black'
    }
    return(
        <div style={style}>
            <p><strong>Email:</strong> {props.contactInfo.email}</p>
            <p><strong>Address:</strong> {props.contactInfo.address}</p>
            <RegButton url={props.contactInfo.githubUrl}>Github</RegButton>
            <RegButton url={props.contactInfo.linkedinUrl}>Linkedin</RegButton>
        </div>
    )
}

export default Contacts;

