import React, {Component} from 'react';
import { Route, BrowserRouter as Router, Switch, Link, Redirect, HashRouter } from 'react-router-dom'
import Profile from './Components/Profile/Profile'
import Projects from './Components/Projects/Projects'
import Footer from './Components/Footer'
import Header from './Components/Header/Header'
import './App.css'
import { data } from './config/config.js'

class App extends Component{
  state = {
    navigators : data.navigators,
    skills : data.skills,
    languages : data.languages,
    educations : data.educations,
    contactInfo : data.contactInfo,
    // slides: data.slides,
    projects: data.projects,
    viewProfile: true,
    viewPorject: true,
  };

  render(){
    let profile = null;
    if(this.state.viewProfile){
      profile = (<Profile 
        name="Lin Chen" 
        skills={this.state.skills} 
        languages={this.state.languages} 
        educationsInfo={this.state.educations}
        contactInfo={this.state.contactInfo}
        >
        I am passionate about learning the new technology.
      </Profile>);
    }

    let projects = null;
    if(this.state.viewPorject){
      projects = (<Projects projects={this.state.projects}/>)
    }

    return (
      <div className="App">
        <HashRouter>
        <Header navigators = {this.state.navigators} />
          <Switch>
          <Route path="/projects">
            {projects}
          </Route>
          <Route path="/profile">
            {profile}
          </Route>
          <Redirect from="/" to="/profile" />
          </Switch>
        <Footer />
        </HashRouter>
      </div>
    );
  }
}

export default App;
